<template>
  <div class="screenCar">
    <!-- 标题 -->
    <van-nav-bar title="选车" :fixed="true" left-text="返回" left-arrow @click-left="onClickLeft" />

    <!-- 价格 -->
    <div class="title">
      <p>价格</p>
    </div>
    <van-row>
      <van-col span="6" v-for="(item, index) in price" :key="index">
        <van-button
          type="default"
          :class="index===index1?'active':null"
          @click="myPrice(index,item.p)"
        >{{item.title}}</van-button>
      </van-col>
    </van-row>

    <!-- 级别 -->
    <div class="title">
      <p>级别</p>
    </div>
    <van-row>
      <van-col span="6" v-for="(item, index) in chexing" :key="index">
        <van-button
          type="default"
          :class="index===index2?'active':null"
          @click="myChexing(index,item.l)"
        >{{item.title}}</van-button>
      </van-col>
    </van-row>

    <!-- 国别 -->
    <div class="title">
      <p>国别</p>
    </div>
    <van-row>
      <van-col span="6" v-for="(item, index) in country" :key="index">
        <van-button
          type="default"
          :class="index===index3?'active':null"
          @click="myCountry(index,item.c)"
        >{{item.title}}</van-button>
      </van-col>
    </van-row>

    <!-- 厂商 -->
    <div class="title">
      <p>厂商</p>
    </div>
    <van-row>
      <van-col span="6" v-for="(item, index) in changshang" :key="index">
        <van-button
          type="default"
          :class="index===index4?'active':null"
          @click="myChangshang(index,item.g)"
        >{{item.title}}</van-button>
      </van-col>
    </van-row>

    <div class="searchButton">
      <van-button type="info" @click="reserButton">重置</van-button>
      <van-button type="info" :disabled="SerialCount==0?true:false" @click="toCarInfo">有{{SerialCount}}款车型符合要求</van-button>
    </div>
  </div>
</template>
<script>
import * as api from "../../api/index.js";
export default {
  data() {
    return {
      price: [
        { title: "5万以下", p: "0-5" },
        { title: "5-8万", p: "5-8" },
        { title: "8-12万", p: "8-12" },
        { title: "12-18万", p: "12-18" },
        { title: "18-25万", p: "18-25" },
        { title: "25-40万", p: "25-40" },
        { title: "40-80万", p: "40-80" },
        { title: "80万以上", p: "80-9999" }
      ],
      chexing: [
        { title: "微型车", l: "1" },
        { title: "小型车", l: "2" },
        { title: "紧凑型", l: "3" },
        { title: "中型车", l: "4" },
        { title: "中大型", l: "5" },
        { title: "豪华车", l: "6" },
        { title: "MPV", l: "7" },
        { title: "SUV", l: "8" }
      ],
      country: [
        { title: "德系", c: "4" },
        { title: "日系", c: "2" },
        { title: "韩系", c: "16" },
        { title: "美系", c: "8" },
        { title: "欧系", c: "484" },
        { title: "非日系", c: "509" }
      ],
      changshang: [
        { title: "国产", g: "1" },
        { title: "合资", g: "2" },
        { title: "进口", g: "4" }
      ],
      list: [],
      SerialCount: 0,
      index1: "",
      index2: "",
      index3: "",
      index4: "",
      p: "",
      l: "",
      c: "",
      g: ""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    async getScreen(p, l, c, g) {
      let data = await api.getScreenCar(p, l, c, g);
      this.list = data.Data.List;
      this.SerialCount = data.Data.SerialCount;
    },
    myPrice(index, p) {
      if (this.index1 === index) {
        return;
      }
      this.index1 = index;
      this.p = p;
      this.getScreen(this.p, this.l, this.c, this.g);
    },
    myChexing(index, l) {
      if (this.index2 === index) {
        return;
      }
      this.index2 = index;
      this.l = l;
      this.getScreen(this.p, this.l, this.c, this.g);
    },
    myCountry(index, c) {
      if (this.index3 === index) {
        return;
      }
      this.index3 = index;
      this.c = c;
      this.getScreen(this.p, this.l, this.c, this.g);
    },
    myChangshang(index, g) {
      if (this.index4 === index) {
        return;
      }
      this.index4 = index;
      this.g = g;
      this.getScreen(this.p, this.l, this.c, this.g);
    },
    reserButton() {
      this.index1 = ""
        this.index2 = ""
        this.index3 = ""
        this.index4 = ""
        this.p = ""
        this.l = ""
        this.c = ""
    this.g = ""
      this.getScreen(this.p, this.l, this.c, this.g)
    },
    toCarInfo(){
        let info = {p:this.p,l:this.l,c:this.c,g:this.g,count:this.SerialCount}
        localStorage.setItem("info",JSON.stringify(info))
        this.$router.push({name:"screen",params:{obj:info}})
    }
  },
  created() {
    this.getScreen();
  }
};
</script>
<style lang="scss">
.screenCar {
  padding: 50px 0 20px;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    color: #fff;
    .van-nav-bar__left {
      .van-nav-bar__text,
      i {
        color: #fff;
      }
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .title {
    color: #888;
    font-size: 14px;
    line-height: 30px;
    height: 30px;
    padding-left: 20px;
    background: #f5f5f5;
  }
  .van-row {
    margin: 0 10px;
    text-align: center;
    .van-button--normal {
      padding: 0;
      width: 70px;
      height: 38px;
      line-height: 38px;
      margin: 8px 0;
    }
    .active {
      background: #f4f7ff;
      border-color: #5f93ff;
      color: #5f93ff;
    }
  }
  .searchButton {
    display: flex;
    justify-content: center;
    margin: 30px 20px 0;
    button {
      &:first-child {
        width: 20%;
        border-color: #68a5ff;
        background-color: #68a5ff;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
      &:last-child {
        width: 80%;
        border-color: #5a99f6;
        background-color: #5a99f6;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
  }
}
</style>